import { useState } from "react";
import { useReactFlow } from "reactflow";
import styled from "styled-components";
import NodeFactory from "./NodeFactory";

const MenuContainer = styled.div`
position: absolute;
top: 0px;
left: 0px;
width: 100%;
margin: 0px auto;
padding: 10px 20px 0px;
display: flex;
flex-direction: row;
z-index: 100;

&:before {
    content: '';
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 40px;
    background: black;
    z-index: -1;
}

ul {
    color: white;
    font-family: roboto;
    font-size: large;
    font-weight: normal;
    text-align: left;
    padding: 0px;
    margin: 0px 10px 0px 0px;
    cursor: pointer;

    &:hover {
        background: hsl(204,63%,25%);
        
        li {
            display: block;
        }
    }

    span {
        padding: 0px 10px 0px 10px;
    }

    
    li {
        display: none;
        background: transparent;
        color: white;
        font-family: roboto;
        font-size: medium;
        font-weight: 100;
        text-align: left;
        padding: 0px 10px 0px 10px;
        margin: 0px;
        cursor: pointer;
         
        &:hover {
            background: hsl(204,63%,30%);
        }
    }

}
`;


const Item = ({title, children}) => {
    const [open, setOpen] = useState(false); 

    return (
        <ul onMouseEnter={ () => setOpen(true)} onMouseLeave={() => setOpen(false)} >
            <span>{title}</span>
            {children}
        </ul>
    );
}

// const Nav = ({addNode}) => {
const Nav = () => {
    const instance = useReactFlow();


    const addNode = (nodeName) => {
        const node = NodeFactory(nodeName);
        const nodes = instance.getNodes();
        instance.setNodes([...nodes, node])
    };

    return (
        <MenuContainer fluid>
            <Item title="Primitives">
                <li onClick={() => addNode('PrimitiveAddress')}>Address</li>
                <li onClick={() => addNode('PrimitiveBoolean')}>Boolean</li>
                <li onClick={() => addNode('PrimitiveNumber')}>Number</li>
                <li onClick={() => addNode('PrimitiveString')}>String</li>
            </Item>
            <Item title="Contracts" >
                <li onClick={() => addNode('ContractERC20')}>ERC-20</li>
                <li onClick={() => addNode('ContractERC3475')}>ERC-3475</li>
                <li onClick={() => addNode('ContractERC721')}>ERC-721</li>
                <li onClick={() => addNode('ContractERC1155')}>ERC-1155</li>
            </Item>
            <Item title="Auxiliaries">
                <li onClick={() => addNode('AuxiliaryRBAC')}>RBAC</li>
                <li onClick={() => addNode('AuxiliaryOracle')}>Oracle</li>
                <li onClick={() => addNode('AuxiliaryMultisig')}>Multisig</li>
                <li onClick={() => addNode('AuxiliaryAuction')}>Auction</li>
                <li onClick={() => addNode('AuxiliaryMarket')}>Market</li>
                <li onClick={() => addNode('AuxiliaryDistribution')}>Distribution</li>
            </Item>
        </MenuContainer>
    );
}

export default Nav;