import {useRef, useState} from 'react';
import styled from 'styled-components';
import {BooleanHandle} from "../../Components/Handles";
import Node from "../Node";

const Selector = styled.div`
    background: hsl(204,63%,20%);
    border: 0px;
    height: 20px;
    border-radius: 10px;
    margin: 10px 30px 10px 10px;
    color: hsl(204,63%,90%);
    font-weight: 100;
    font-size: 12px;
    padding: 5px;
    width: 100px;

    &:not(.selected):before {
        content: "Off";
        display: block;
        padding: 0px 15px;
        font-size: 12px;
        font-weight: bold;
        width: 50px;
        height: 20px;
        border-radius: 10px 0px 0px 10px;
        background: hsl(204,63%,40%);
        margin: -5px;
    }

    &.selected:before {
        content: "On";
        display: block;
        padding: 0px 15px;
        font-size: 12px;
        font-weight: bold;
        width: 50px;
        height: 20px;
        border-radius: 0px 10px 10px 0px;
        background: hsl(204,63%,40%);
        margin: -5px 10px -10px 45px;
    }

`

const PrimitiveBooleanNode = ({id, parent, data}) => {
    const elRef = useRef(null);
    const [state, setState] = useState(false);

    data.title = "Booblean";
    data.value = state;

    return <>
        <Node title={data.title}>
            <div>
                <Selector className={`${state ? 'selected' : ''}`} state={state} ref={elRef} onClick={() => setState(!state)} />
                <BooleanHandle io="output" id={id} parent={elRef} />
            </div>
        </Node>
    </>;
}

export default PrimitiveBooleanNode