const NodeFactory = (nodeName) => {
    const now = Date.now();

    let node = {
        id: `node-${nodeName}-${now}`,
        position: { x:  100, y: 100  },
        type: nodeName,
        draggable: true,
        isDraggable: true,
        data: {}
    };

    return node;
}

export default NodeFactory;