import { Children } from "react";
import {Handle as ReactFlowHandle, Position} from "reactflow";

const Handle = ({io, id, parent, children}) => {
    if (!parent || !parent.current) {
        return null;
    }

    const top = parent.current.offsetTop;
    const left = io === 'output' ? parent.current.offsetLeft + parent.current.offsetWidth + 10 : parent.current.offsetLeft - 30;

    return <>
        <ReactFlowHandle 
            type={io === "output" ? "source" : "target"}
            position={io === 'output' ? Position.Right : Position.Left}
            id={`${id}_${io}`}
            isConnectable={true}
            style={{
                position: 'absolute',
                top: top + 10, 
                left: left,
                border: '0px',
                width: '20px', 
                height: '20px', 
                background: 'transparent', 
                color: 'transparent',
                padding: '0px',
                margin: '0px',
                zIndex: '100',
            }} 
        >
        </ReactFlowHandle>
        <svg 
            style={{
                position: 'absolute',
                top: top, 
                left: left,
                margin: '0px',
                width: '20px',
                height: '20px',
                background: 'transparent',
                fill: 'hsl(204,63%,85%)',
            }} 
        >
            {children}
        </svg>
    </>;

}

export default Handle;

