import { useMemo, useState } from 'react';
import ReactFlow, { Background, Controls, Position, useReactFlow } from 'reactflow';
import 'reactflow/dist/style.css';
import Nav from './Nav';

const Flow = ({width, height, nodes, nodesTypes, edges}) => {

    const edgeOptions = {
        animated: false,
        style: {
            stroke: 'white',
            strokeWidth: 3,
        },
    };
    

    return (
        <div style={{width, height}}>
            <ReactFlow  
                // defaultEdgeOptions={edgeOptions}
                defaultNodes={[]}
                defaultEdges={[]}
                nodeTypes={nodesTypes}
                style={{
                    background: 'black',
                }}
            > 
                <Nav />
                <Background />
                <Controls />
            </ReactFlow>
        </div>
    );
}
  

export default Flow;