import {useRef} from 'react';
import styled from 'styled-components';
import {NumberHandle} from "../../Components/Handles";
import Node from "../Node";

const Input = styled.input`
background: hsl(204,63%,20%);
border: 0px;
height: 20px;
border-radius: 10px;
margin: 10px 45px 10px 10px;
color: hsl(204,63%,90%);
font-weight: 100;
font-size: 12px;
padding: 5px;
`;

const PrimitiveAddressNode = ({id, parent, data}) => {
    const elRef = useRef(null);

    data.title = "Number";
    data.value = "0";

    return <>
        <Node title={data.title}>
            <div>
                <Input
                    type="number"
                    ref={elRef}
                    // value={data.value}
                    // onChange={e => setData({...data, value: e.target.value})} 
                />
                    <NumberHandle io="output" id={id} parent={elRef} />
            </div>
        </Node>
    </>;
}

export default PrimitiveAddressNode;