import styled from "styled-components";

const Title = styled.section`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: hsl(204,63%,20%);
    width: 100%;
    margin: 0px;

    &:before {
        content: "${props => props.title}";
        padding-top: 10px;
        color: hsl(204,63%,90%);
        font-weight: 100;
        font-size: 15px;
        height: 40px;
        width: 100%;
        text-align: center;
        background: hsl(204,63%,10%);
    }

`;

const Section = ({title, show, children}) => {
    return <>
        {show && 
            <Title title={title}>
                {children}
            </Title>
        }
    </>;
}

export default Section;