import {useRef, useState} from 'react';
import styled from 'styled-components';
import {AddressHandle, NumberHandle, BooleanHandle} from "../../Components/Handles";
import Node from "../Node";
import Block from './Block';
import Section from "./Section";
import Element from './Element';

const Plus = styled.div`
    content: "+";
    width: 80%;
    background-color: hsl(204, 63%, 18%);
    color: white;
`;

const Selector = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 5px 30px 5px 30px;
    font-size: 12px;

    * {
        background: transparent;
        color: hsl(204,43%,90%);
        border-radius: 10px;
        width: 20;
        height: 20;
        cursor: pointer;
        padding: 0px 5px;

        &.selected {
            background: hsl(204,63%,40%);

        }
    }
`;

const ContractERC20 = ({id, parent, data}) => {
    const elRef = useRef(null);
    const [showAttributes, setShowAttributes] = useState(true);
    const [showMethods, setShowMethods] = useState(true);
    const [showEvents, setShowEvents] = useState(true);
    const [showParents, setShowParents] = useState(true);
    const [showOutputs, setShowOutputs] = useState(false);

    data.title = "ERC 20";

    return <>
        <Node title={data.title} type='contract'>
            <Section title="Attributes" show={showAttributes}>
                <Block title="ERC20">
                    <Element name="Name" io="input" outputHandle={NumberHandle}/>
                    <Element name="Symbol" io="input" outputHandle={NumberHandle}/>
                </Block>
                <Plus />
            </Section>
            <Section title="Methods" show={showMethods}>
                <Block title="Transfer">
                    <Element name="To" io="input" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="input" outputHandle={NumberHandle}/>
                    <Element name="Success" io="output" inputHandle={BooleanHandle}/>
                </Block>
                <Block title="TransferFrom">
                    <Element name="From" io="input" outputHandle={AddressHandle}/>
                    <Element name="To" io="input" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="input" outputHandle={NumberHandle}/>
                    <Element name="Success" io="output" inputHandle={BooleanHandle}/>
                </Block>
                <Block title="Balance Of">
                    <Element name="Owner" io="input" outputHandle={AddressHandle}/>
                    <Element name="Balance" io="output" inputHandle={NumberHandle}/>
                </Block>
                <Block title="Allowance">
                    <Element name="Owner" io="input" outputHandle={AddressHandle}/>
                    <Element name="Spender" io="input" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="output" inputHandle={NumberHandle}/>
                </Block>
                <Block title="Approve">
                    <Element name="Spender" io="input" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="input" outputHandle={NumberHandle}/>
                </Block>
                <Block title="Total Supply">
                    <Element name="Amount" io="output" inputHandle={NumberHandle}/>
                </Block>
            </Section>
            <Section title="Events" show={showEvents}>
                <Block title="Transfer">
                    <Element name="From" io="output" outputHandle={AddressHandle}/>
                    <Element name="To" io="output" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="output" outputHandle={NumberHandle}/>
                </Block>
                <Block title="Approval">
                    <Element name="Owner" io="output" outputHandle={AddressHandle}/>
                    <Element name="Spender" io="output" outputHandle={AddressHandle}/>
                    <Element name="Amount" io="output" outputHandle={NumberHandle}/>
                </Block>
            </Section>
            <Section title="Parents" show={showParents}>
                <Block title="ERC20" />
                <Block title="RBAC" />
                <Block title="Distribution" />
                <Block title="Pausable" />               
            </Section>
            <Section title="Outputs" show={showOutputs}></Section>
            <Selector>
                <span className={showAttributes ? 'selected': ''} onClick={() => setShowAttributes(!showAttributes)}>A</span>
                <span className={showMethods ? 'selected': ''} onClick={() => setShowMethods(!showMethods)}>M</span>
                <span className={showEvents ? 'selected': ''} onClick={() => setShowEvents(!showEvents)}>E</span>
                <span className={showParents ? 'selected': ''} onClick={() => setShowParents(!showParents)}>P</span>
                <span className={showOutputs ? 'selected': ''} onClick={() => setShowOutputs(!showOutputs)}>O</span>
            </Selector>
        </Node>
    </>;
}

export default ContractERC20;