import styled from "styled-components"

const BlockStyle = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: hsl(204,63%,30%);
    width: 95%;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;

    &:before {
        content: "${props => props.title}";
        padding-top: 0px;
        color: hsl(204,63%,80%);
        font-weight: 100;
        font-size: 12px;
        height: 20px;
        width: 100%;
    }
`;

const Block = ({ title, children, inputHandle, outputHandle }) => {
    return (
        <BlockStyle title={title}>
            {children}
        </BlockStyle>
    );
}

export default Block;