import { useState } from "react";
import styled from "styled-components";

const Frame = styled.div`
    border: 0px solid transparent;
    padding: 0px 0px 20px 0px;
    margin: 0px;
    min-width: ${props => props.width}px;}; 
    border-radius: 20px;
    background: hsl(204,63%,10%);

    .selected & {
        box-shadow: 0px 0px 3px 3px hsl(204,63%,40%);
    }
`;

const Title = styled.div`
    padding: 5px;
    background: hsl(204,63%,25%);
    border-radius: 20px 20px 0px 0px;
    border-bottom: 3px solid hsl(204,63%,35%);
    display: flex;
    flex-direction: row;
    justify-content: center;
`;

const Body = styled.div`
    color: white;
    margin: 0px;
    padding: 0px;
    background: hsl(204,63%,15%);
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const TitleEdit = styled.input`
    color: white;
    font-family: roboto;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    background: transparent;
    border: 0px;
    padding: 0px;
    border-radius: 20px;
    width: 150px;
    margin: auto;
`;

const Node = ({title, type, children}) => {
    const [_title, setTitle] = useState(title);
    let width = 150;

    if(type === 'contract') {
        width = 250;
    }
    
    return (
        <Frame width={width}>
            <Title>
                <TitleEdit type="text" value={_title} onChange={e => setTitle(e.target.value)} />
            </Title>
            <Body>{children}</Body>
        </Frame>
    );
}

export default Node;