import { useRef } from 'react';
import styled from "styled-components";

const ElementStyle = styled.div`    
    margin: 2px 0px;
    padding: 3px 30px 3px 30px;
    text-align: ${props => props.align};
    background: hsl(204,53%,40%);
    color: hsl(204,43%,90%);
    width: 100%;
    font-size: 12px;
    border-radius: 5px;
`;

const Element = ({name, io, inputHandle, outputHandle}) => {
    const elRef = useRef(null);

    return <>
        <ElementStyle ref={elRef} align={io === 'output' ? 'right' : 'left'}>
            {name}
        </ElementStyle>
        {inputHandle && <inputHandle io='input' id={123} parent={elRef} />}
        {outputHandle && <outputHandle io='output' id={1234} parent={elRef} />}
    </>;
}

export default Element;